import React from 'react';
import Nav from '../components/Nav';
import Hero from '../components/Hero';
import Map from '../components/Map';
import ContactUs from '../components/ContactUs';
import Footer from '../components/Footer';
import Container from '../components/Container';
import CallToAction from '../components/CallToAction';
import BeforeAfter from '../components/BeforeAfter';

function Acne() {
    return (
        <div>
            <Nav />

            <Hero
                hidePricing={true}
                isSelector={true}
                tags={['WE ARE THE EXPERTS', 'ACNE TREAMENTS']}
                imgs={['/images/acne_slider1.jpg', '/images/acne_slider2.jpg']} />

            <section key="0" className="section">
                <div className="columns is-centered is-mobile">
                    <Container>
                        <h1 className="welcome-title has-text-black">welcome to Undo</h1>
                        <p className="is-size-3 has-workflow-spacing is-double">
                            Undo, Skincare Center uses a three-prong approach to treating acne. Results are typically
                            seen <b><span className="has-text-link">two-weeks</span></b> after the first treatment.
                        </p>
                    </Container>
                </div>
            </section>

            <CallToAction key="1" text='To learn more about our acne treatment or to schedule a free consultation with one of our skincare experts, call <a href="tel:8169748636">(816) 974-8636</a>, or book your appointment online.' />

            <section className="section" key="0" id="treatments">
                <div className="columns is-centered is-mobile">
                    <Container>
                        <h1 id="how_it_works" className="welcome-title has-text-black">acne treatments</h1>

                        <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">teenage, adult or wearing a mask</h5>
                        <p className="has-workflow-spacing is-double">
                            Teenagers, adults, anyone wearing a mask may suffer from acne breakouts.
                            Undo, Skincare offers Intense Pulsed Light (IPL) treatments for acne and scarring.
                            These treatments have been taking the skincare industry by storm in recent years.
                        </p>

                        <div className="columns is-vcentered has-workflow-spacing is-double">
                            <div className="column is-4 has-text-centered">
                                <img src="/images/mask.jpg" alt="PiQo" />
                            </div>
                            <div className="column">
                                <div>
                                    <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">acne treatment</h5>
                                    <p>
                                        Undo, Skincare Center uses a three-prong approach to treating acne.  This approach
                                        includes daily use of a purifying cleanser to make sure that your skin is clean and not oily.
                                        Next, Oil-free Board-Spectrum SPF 46 with Zinc to help calm and protect sensitive
                                        skin types prone to discoloration and breakouts associated with acne and rosacea.
                                        And lastly, a series of IPL Acne treatments, taking less than twenty-minutes each.
                                        Results are typically seen two-weeks after the first treatment.
                                    </p>
                                </div>
                            </div>
                        </div>

                        <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">over the counter</h5>
                        <p className="has-workflow-spacing is-double">
                            If you've tried over-the-counter (nonprescription) acne products designed to clear skin become clogged with oil, dirt,
                            and bacteria for several weeks and they haven't helped, it may be time to look into professional acne treatments to
                            make your skin brighter and clearer without the potential collateral damage of prescribed medications.
                        </p>

                        <div className="columns is-vcentered has-workflow-spacing is-double">
                            <div className="column">
                                <div>
                                    <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">medications</h5>
                                    <p>
                                        Acne drugs may be effective for one or more of the causes of acne, however these drugs can cause collateral damage and
                                        may result in nausea, vomiting, diarrhea, mood changes, changes in vision, and permanent scarring.
                                    </p>
                                </div>
                            </div>
                            <div className="column is-4 has-text-centered">
                                <img src="/images/medications.jpg" alt="Medications" />
                            </div>

                        </div>

                        <h5 className="subtitle is-4 has-text-weight-bold has-text-black has-workflow-spacing">acne scars</h5>
                        <p className="has-workflow-spacing is-double">
                            Living with acne scars can be physically and emotionally difficult for people of all ages.  The Lumenis ResurFX, is
                            the latest in non-ablative, fractional laser technology to improve skin discoloration, stretch marks, acne scars,
                            wrinkles, and more.  Non-ablative means it does not destroy or ablate the skin cells, while fractional means it is only
                            affecting a fraction or percentage of the skin.  The benefit of the ResurFX is that it is able to get great results
                            with minimal recovery time.
                        </p>


                    </Container>
                </div>
            </section>

            <BeforeAfter />

            <Map />
            <ContactUs />
            <Footer link="/acne#" />
        </div>
    );
}

export default Acne;
